import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA, MatDialog } from '@angular/material/dialog';
import { Pack } from 'src/app/interfaces/pack-credit.interface';
import { ClientService } from 'src/app/services/client/client.service';
import { CreditService } from '../../services/credit/credit.service';
import { LocalStorageService } from 'ngx-localstorage';
import { NotificationService } from '../../services/notification/notification.service';
import { PaiementService } from '../../services/paiement/paiement.service';
import { CentralPayComponent } from '../central-pay/central-pay.component';
import { isBoolean } from 'util';

@Component({
  selector: 'app-pack-modal',
  templateUrl: './pack-modal.component.html',
  styleUrls: ['./pack-modal.component.scss']
})
export class PackModalComponent implements OnInit {
  currentOnglet = null;
  currentSelected = null;
  montantSelected: number = null;
  creditSelected: number = null;

  currentPack: Pack[] = [];

  clientId: number = null;
  credit: number = 0;
  creditId: number = 0;

  listPack: string [] = this.creditService.listPack;

  packBienvenue: Pack[] = this.creditService.packBienvenue;

  packBronze: Pack[] = this.creditService.packBronze;

  packArgent: Pack[] = this.creditService.packArgent;

  packOr: Pack[] = this.creditService.packOr;

  designationPack: string = null;

  constructor(
    private creditService: CreditService,
    private clientService: ClientService,
    private paiementService: PaiementService,
    private store: LocalStorageService,
    private notificationService: NotificationService,
    public dialogRef: MatDialogRef<PackModalComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
    private dialog: MatDialog
  ) {

  }

  ngOnInit(): void {
    this.getMyCredit();
    // this.currentPack = this.packBienvenue;
  }

  // Get client credit
  async getMyCredit() {
      const id = parseInt(this.store.get('identification//'));
      await this.clientService.getMyInfos().subscribe(
        (data) => {
          // console.log('Mon credit ', data);
          this.credit = data.credit ? data.credit.credit : 0;
          this.creditId = data.credit ? data.credit.id : 0;
          this.clientId = data.id;
        }
      )
  }

  choosePack(id: number) {
    this.currentOnglet = id;
    this.currentSelected = null;
    this.montantSelected = null;
    this.creditSelected = null;

    switch (id) {
      case 0:
        this.currentPack = this.packBienvenue;
        this.designationPack = 'bienvenue';
        break;

      case 1:
        this.currentPack = this.packBronze;
        this.designationPack = 'bronze';
        break;

      case 2:
        this.currentPack = this.packArgent;
        this.designationPack = 'argent';
        break;

      case 3:
        this.currentPack = this.packOr;
        this.designationPack = 'or';
        break;

      default:
        break;
    }
  }

  chooseMontant(index: number, montant: number, credit: number) {
    this.currentSelected = index;
    this.montantSelected = montant;
    this.creditSelected = credit;
  }

  onNoClick(): void {
    this.dialogRef.close();
  }

  async validate(): Promise<void> {
    if ((this.currentSelected == null) || (this.montantSelected == null) || (this.creditSelected == null)) return null;
    await this.buyPack(this.montantSelected, this.creditSelected, this.designationPack);
  }

  async buyPack(montant: number, credit: number, designation: string) {
    if (this.creditId === 0) return false;
    if (designation === 'bienvenue') {
      this.paiementService.verifyPack().subscribe(
        (data: any) => {
          if (data.error) {
            this.notificationService.infoMsg(data.message);
          } else {
            this.openCentralPayModal(montant, credit, designation);
          }
        }, (error) => {
          console.log(error)
        }
      )
    } else {
      this.openCentralPayModal(montant, credit, designation);
    }
  }

  openCentralPayModal(montant: number, credit: number, designation: string) {
    const dialogRef = this.dialog.open(CentralPayComponent, {
      width: '90vh',
      data: {
        montant,
        credit,
        designation,
        creditId: this.creditId,
        id: this.clientId
      }
    });

    dialogRef.afterClosed().subscribe(result => {
      if (isBoolean(result)) {

      } else {
        if (result === 'good') {
          this.getMyCredit().then(
            (data) => {
              this.paiementService.buyPack(credit).subscribe(
                (data) => {
                  this.notificationService.success(`Pack ${this.listPack[this.currentOnglet]}`,
                  `Vous avez obtenu ${credit} crédits de plus.`);
                }, (error) => {
                  console.log(error)
                }
              )
              this.dialogRef.close();
            }
          );
        }
      }
    });
  }

}
